const {__} = wp.i18n;

const attributes = Object.assign({},
	{
		style: {
			type: 'string',
			default: '1',
		},
		title: {
			type: 'string',
			default: '',
		},
		text: {
			type: 'string',
			default: '',
		},
	}
);

export default attributes;



